  methods:{
      filterRecipes() {
           let filtered = []
           console.log("PLZ WORK")
           this.$root.$data.recipes.forEach(recipe => {
               console.log(recipe.name)
               recipe.keywords.every(keyword => {
                    console.log(keyword)
                    if (keyword.search(this.searchText.toLowerCase())>=0){
                        console.log(keyword)
                        filtered.push(recipe)
                        return false;
                    }
                    else{
                        return true;
                    }
               });
               
           });
           /*this.$root.$data.filtered = this.$root.$data.recipes.filter(recipe => recipe.keywords.includes(this.searchText.toLowerCase()));*/
           this.$root.$data.filtered = filtered;
           this.$root.$data.searching = true;
           this.$router.push({
                name: 'Search',
                params: { data: this.searchText}
            });
           /*this.$router.push('/search-results/' + this.searchText);*/
      }
  },
